
.graph-container{
 position: relative;
 height:50vh;
 max-width: 1500px;
 padding: 0 36px 0 32px;
 @media (min-width: 768px) {
   height:70vh;
 }
 @media (min-width: 1000px) {
   padding: 0 126px 0 122px;
 }
}

.container-text-graphic {
  padding: 32px;
}

.text-graphic {
  @media (min-width: 900px) {
    padding-right: 300px;
    padding-left: 300px;
  }
}
